export function getRandomString(length = 10): string {
    let result = '';
    const characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
    for (let i = 0; i < length; i++) {
        result += characters.charAt(Math.floor(Math.random() * characters.length));
    }
    return result;
}

export function multipleSplitString(string: string, splits: Array<string>) {
    let result = [] as Array<string>;

    for (const i in splits) {
        if (string.includes(splits[i])) {
            result = result.concat(string.split(splits[i]))
        }
    }

    if (result.length === 0) {
        result = [string];
    }

    result = result.filter((value) => value !== '');

    return result;
}

export function searchStringInStrings(queryStrings: Array<string>, searchStrings: Array<string>): Array<string> {
    const result = [] as Array<string>;
    for (const i in searchStrings) {

        let includes = true;
        for (const q in queryStrings) {
            if (includes && !searchStrings[i].includes(queryStrings[q])) {
                includes = false;
            }
        }

        if (includes) {
            result.push(searchStrings[i])
        }
    }
    return result;
}