
import {defineComponent} from "vue";
import axios from "axios";
import {multipleSplitString, searchStringInStrings} from "@/utils/StringUtils";

export default defineComponent({
  name: "SearchInput",
  props: {
    url: String,
    modelValue: String
  },
  data() {
    return {
      query: '' as string,
      searchStrings: [] as Array<string>,
      resultStrings: [] as Array<string>
    }
  },

  async created() {
    this.startAssignQuery()
    this.getSearchStrings()
  },

  watch: {
    query(newValue) {
      const querySplits = multipleSplitString(newValue, [' ', '_']);
      this.resultStrings = searchStringInStrings(querySplits, this.searchStrings);
      this.$emit('update:modelValue', newValue)
    }
  },

  methods: {
    startAssignQuery() {
      if (this.modelValue === undefined) {
        return
      }
      this.query = this.modelValue
    },

    getSearchStrings() {
      if (this.url === undefined) {
        console.error('search input url is undefined.')
        return
      }
      axios.get(this.url)
          .then(res => {
            this.searchStrings = res.data;
          })
          .catch(error => (console.log(error)))
    },

    selectDropdown(dropdownString: string) {
      this.query = dropdownString
    }
  }
})
