<template>
  <div class="row row-cols-2 justify-content-between">
    <!--     행동절차     -->
    <div class="col-3">
      <Draggable :treeData="treeData" :ondragend="onDragEnd" idKey="id" parentIdKey="pid">
        <template v-slot="{ node, tree }">

          <template v-if="node.$level === 1">
            <div class="mb-1 d-flex justify-content-between">
              <h4 class="">행동절차</h4>
              <div class="p-0 d-flex flex-nowrap max-h-row">
                <button class="btn btn-primary me-1" v-on:click="add_action(tree, 'RANDOM')">
                  <font-awesome-icon icon="shuffle"/>
                </button>
                <button class="btn btn-primary me-1" v-on:click="add_action(tree)">
                  <font-awesome-icon icon="plus"/>
                </button>
                <button class="btn btn-primary" v-on:click="save_action_tree(tree)">
                  <font-awesome-icon icon="floppy-disk"/>
                </button>
              </div>
            </div>
            <hr/>
            <div class="mb-3 d-flex justify-content-between row">
              <div class="col-12" style="position: relative">
                <input @focus="isActionSearchFocus=true" @blur="isActionSearchFocus=false"
                    type="text" class="form-control" v-model="addActionName">
                <div v-if="isActionSearchFocus" class="list-group" style="position: absolute; z-index: 1; width: 100%; top: 100%">
                  <button v-bind:key="actionRow.action_seq" v-for="actionRow in actionSearchRowList" type="button" class="list-group-item list-group-item-action">{{actionRow.name}}</button>
                </div>
              </div>
            </div>
          </template>

          <div class="mt-3 mb-3 d-flex justify-content-between">
            <h5 class="mb-auto mt-auto w-100" v-bind:style="[node.isSelected ? {'color':'red'} : '']" v-on:click="select_action(node, tree);">
              <font-awesome-icon v-bind:style="[node.action.action_type === 'RANDOM' ? '' : {'display':'none'}]" class="w-auto" icon="shuffle"/>
              {{ node.action.name }}
            </h5>
            <button class="btn btn-light me-1 min-w-btn max-h-row" data-bs-toggle="collapse" :data-bs-target="`#collapse`+node.$id">
              <font-awesome-icon icon="caret-down"/>
            </button>
            <button v-bind:style="[node.$level !== 1 ? '' : {'visibility':'hidden'}]"
                    class="btn btn-light min-w-btn max-h-row" v-on:click="remove_action(node, tree)">
              <font-awesome-icon icon="minus"/>
            </button>
            </div>
          <div class="collapse row text-center" :id="`collapse`+ node.$id">
            <div class="col-3">
              <small class="fw-light">repeat</small>
              <input type="number" class="form-control text-center" v-model="node.action.repeat_times">
            </div>
          </div>
        </template>
      </Draggable>

      <hr/>

    </div>
    <!--     매크로 리스트     -->
    <div class="col-9">
      <div class="row text-center mb-1">
        <h4 class="col-11 text-start">매크로 리스트</h4>
        <div class="col-1 d-flex flex-nowrap max-h-row">
          <button class="btn btn-primary min-w-btn me-1" v-on:click="add_macro">
            <font-awesome-icon icon="plus"/>
          </button>
          <button class="btn btn-primary min-w-btn" v-on:click="save_macro_list">
            <font-awesome-icon icon="floppy-disk"/>
          </button>
        </div>
      </div>
      <hr/>

      <draggable :list="macroRowList" item-key="id">
        <template #item="{ element, index }">
          <div class="row-items mb-3">
            <div class="row text-center mb-2">
              <!--        name        -->
              <div class="col-2">
                <small class="fw-light mb-1">name</small>
                <div class="input-group">
                  <button type="button" class="btn btn-outline-secondary min-w-btn" v-on:click="run_macro(element)">
                    <font-awesome-icon icon="play"/>
                  </button>
                  <input type="text" class="form-control" v-model="element.macro.name">
                </div>
              </div>

              <!--        driverType        -->
              <div class="dropdown col-1">
                <small class="fw-light mb-1">driverType</small>
                <select class="form-select" aria-label=".form-select-lg" v-model="element.macro.driver_type">
                  <option v-for="driverTypeEnum in this.macroEnums['driverType']" v-bind:key="driverTypeEnum.key"
                          :value="driverTypeEnum.value">{{driverTypeEnum.key}}</option>
                </select>
              </div>

              <!--        macroType        -->
              <div class="dropdown col-2">
                <small class="fw-light mb-1">macroType</small>
                <select class="form-select" aria-label=".form-select-lg" v-model="element.macro.macro_type">
                  <option v-for="macroTypeEnum in this.macroEnums['macroType']" v-bind:key="macroTypeEnum.key"
                          :value="macroTypeEnum.value">{{ macroTypeEnum.key }}</option>
                </select>
              </div>

              <!--        element        -->
              <div class="col-5">
                <small class="fw-light mb-1">element</small>
                <div class="input-group w-100 d-flex flex-nowrap">
                  <button type="button" class="btn btn-outline-secondary min-w-btn" v-on:click="add_element(element)">
                    <font-awesome-icon icon="plus"/>
                  </button>
                  <div class="form-select text-start bg-light text-truncate" style="width: 20%;" aria-label=".form-select-lg"
                       v-text="element.elementRows.length > 0 ? element.elementRows[0].element.type : ''"/>
                  <div class="form-control text-start bg-light text-truncate" style="width: 50%;"
                       v-text="element.elementRows.length > 0 ? element.elementRows[0].element.value : ''"/>
                  <div class="form-control bg-light"
                       v-text="element.elementRows.length > 0 ? element.elementRows[0].element.index : ''"/>
                  <button type="button" class="btn btn-outline-secondary min-w-btn rounded-end" id="dropdownMenuReference"
                          data-bs-toggle="dropdown" data-bs-reference="parent" v-on:click="select_macro(element)" data-bs-auto-close="outside">
                    <font-awesome-icon icon="caret-down"/>
                  </button>
                  <span class="position-absolute top-0 start-100 translate-middle badge rounded-pill bg-secondary" style="z-index: 3;">
                    {{element.elementRows.length}}</span>

                  <!--        element dropdown          -->
                  <ul v-show="element.elementRows.length > 0" class="dropdown-menu p-2 bg-light" aria-labelledby="dropdownMenuReference">

                    <!--          element edit          -->
                    <li>
                      <draggable :list="element.elementRows" item-key="id">
                        <template #item="{ element, index }">
                          <div class="input-group pb-2 pt-2" :id="getRandomString">
                            <button class="btn btn-secondary min-w-btn" v-on:click="remove_element(index);">
                              <font-awesome-icon icon="minus"/>
                            </button>
                            <select class="form-select" style="width: 20%;" aria-label=".form-select-lg" v-model="element.element.type">
                              <option v-for="elementTypeEnum in this.elementEnums['elementType']" v-bind:key="elementTypeEnum.key"
                                      :value="elementTypeEnum.value">{{ elementTypeEnum.key }}</option>
                            </select>
                            <input type="text" class="form-control" style="width: 50%;" v-model="element.element.value">
                            <input type="number" class="form-control" v-model="element.element.index">
                          </div>
                        </template>
                      </draggable>
                    </li>
                  </ul>
                </div>
              </div>

              <!--        operator        -->
              <div class="dropdown col-1">
                <small class="fw-light mb-1">operator</small>
                <select class="form-select" aria-label=".form-select-lg" v-model="element.macro.macro_operator">
                  <option v-for="macroOperatorEnum in this.macroEnums['macroOperator']" v-bind:key="macroOperatorEnum.key"
                          :value="macroOperatorEnum.value">{{ macroOperatorEnum.key }}</option>
                </select>
              </div>

              <!--        delete        -->
              <div class="col-1">
                <small class="mb-1 invisible">delete</small>
                <div class="d-flex flex-nowrap max-h-row">
                  <button class="btn min-w-btn me-1 invisible">
                  <font-awesome-icon icon="caret-down"/>
                </button>
                <button class="btn btn-light min-w-btn" v-on:click="remove_macro(index)">
                  <font-awesome-icon icon="minus"/>
                </button>
                </div>
              </div>
            </div>

            <!--       next row        -->
            <div class="row text-center">

              <!--        function name        -->
              <div class="col-2">
                <small class="fw-light mb-1">function name</small>
                <search-input v-model="element.macro.function_name" :key="element.macro.name"
                              url="http://127.0.0.1:8082/api/automation/action/macro/function-names"/>
              </div>

              <!--        variable        -->
              <div class="col-4">
                <small class="fw-light mb-1">variable</small>

                <div class="input-group">
                  <button class="btn btn-outline-secondary min-w-btn" v-on:click="add_variable(element)">
                    <font-awesome-icon icon="plus"/>
                  </button>
                  <div class="form-control text-start bg-light text-truncate"
                       v-text="element.variableRows.length > 0 ? element.variableRows[0].key : ''"/>
                  <div class="form-control text-start bg-light text-truncate w-50"
                       v-text="element.variableRows.length > 0 ? element.variableRows[0].value : ''"/>
                  <button type="button" class="btn btn-outline-secondary min-w-btn rounded-end"
                          id="variableDropdown"
                          data-bs-toggle="dropdown" data-bs-reference="parent" v-on:click="select_macro(element)"
                          data-bs-auto-close="outside">
                    <font-awesome-icon icon="caret-down"/>
                  </button>
                  <span class="position-absolute top-0 start-100 translate-middle badge rounded-pill bg-secondary"
                        style="z-index: 3;">
                    {{ element.variableRows.length }}</span>
                  <!--        variable dropdown          -->
                  <ul v-show="element.variableRows.length > 0" class="dropdown-menu p-2 bg-light"
                      aria-labelledby="variableDropdown">
                    <!--        variable edit            -->
                    <li>
                      <draggable :list="element.variableRows" item-key="id">
                        <template #item="{ element, index }">
                          <div class="input-group pb-2 pt-2" :id="getRandomString">
                            <button class="btn btn-secondary min-w-btn" v-on:click="remove_variable(index);">
                              <font-awesome-icon icon="minus"/>
                            </button>
                            <input type="text" class="form-control" v-model="element.key">
                            <input type="text" class="form-control w-50" v-model="element.value">
                          </div>
                        </template>
                      </draggable>
                    </li>
                  </ul>
                </div>
              </div>

              <!--       wait        -->
              <div class="col-2">
                <small class="fw-light mb-1">wait</small>
                <div class="input-group">
                  <input type="number" class="form-control" v-model="element.macro.min_wait_sec">
                  <span class="input-group-text">~</span>
                  <input type="number" class="form-control" v-model="element.macro.max_wait_sec">
                </div>
              </div>

              <!--        repeat times        -->
              <div class="col-1">
                <small class="fw-light mb-1">repeatTimes</small>
                <input type="number" class="form-control text-center" v-model="element.macro.repeat_times">
              </div>

              <!--        retry times        -->
              <div class="col-1">
                <small class="fw-light mb-1">retryTimes</small>
                <input type="number" class="form-control text-center" v-model="element.macro.retry_times">
              </div>

              <!--       retry wait        -->
              <div class="col-1">
                <small class="fw-light mb-1">retryWait</small>
                <input type="number" class="form-control text-center" v-model="element.macro.retry_wait_sec">
              </div>

              <!--       weight        -->
              <div class="col-1">
                <small class="fw-light mb-1">weight</small>
                <input type="range" class="form-range" min="0" max="100" id="weightRange" v-model="element.macro.macro_weight">
              </div>
            </div>
          </div>
        </template>
      </draggable>

      <hr class="m-0"/>

    </div>
  </div>

</template>

<script>
import {defineComponent} from "vue";
import axios from "axios";
import Action from "@/model/Action";
import Macro from "@/model/Macro";
import Element from "@/model/Element";
import {Draggable} from '@he-tree/vue3'
import draggable from "vuedraggable";
import SearchInput from "@/components/props/input/SearchInput";
import ActionNode from "@/components/automation/action/model/ActionNode";
import MacroRow from "@/components/automation/action/model/MacroRow";
import ElementRow from "@/components/automation/action/model/ElementRow";
import VariableRow from "@/components/automation/action/model/VariableRow";
import {getRandomString} from "@/utils/StringUtils";

export default defineComponent({
  name: "AutomationActionTree",

  components: {
    Draggable, draggable, SearchInput
  },

  data() {
    return {
      udid: this.$route.query.udid,
      queryActionSeq: this.$route.query.actionSeq,
      macroEnums: {},
      elementEnums: {},
      addActionName: '',
      selectedActionNode: null,
      selectedMacroRow: null,
      treeData: [],
      isActionSearchFocus: false,
      actionSearchRowList: [],
      macroRowList: [],
      getActionsParameters: {
        query: 'test1',
        is_like: false
      },
      runMacroParameters: {
        udid: '',
        macro: null
      },
      getRandomString: getRandomString
    }
  },

  async created() {
    this.get_action();
    this.get_macro_enums();
    this.get_element_enums();
  },

  methods: {

    /**
     * action
     */
    get_action() {
      axios.get('http://127.0.0.1:8082/api/automation/action/' + this.queryActionSeq)
           .then(res => {this.treeData = [new ActionNode(res.data)];})
           .catch(error => (console.log(error)))
    },

    select_action(node, tree) {
      this.macroRowList = node.action.macros.map(macro => new MacroRow(macro))
      tree.nodes.forEach(function (node) {node.isSelected = false})
      node.isSelected = true
      this.selectedActionNode = node;
    },

    add_action(tree, actionType = "DEFAULT") {
      let action = new Action({name: this.addActionName, action_type: actionType})
      console.log(action)
      const node = new ActionNode(action)
      tree.addNode(node, tree.nodes[0].$id)
      this.addActionName = ''
    },

    remove_action(node, tree) {
      tree.removeNode(node)
    },

    save_action_tree(tree) {
      const action = Action.ofActionNode(tree.rootNode.$children[0])

      axios.post('http://127.0.0.1:8082/api/automation/action/action', action)
           .then(res => {this.get_action(); alert('저장되었습니다.')})
           .catch(error => (console.log(error)))
    },

    /**
     * macro
     */
    get_macro_enums() {
      axios.get('http://127.0.0.1:8082/api/utils/enum/macro')
          .then(res => {this.macroEnums = res.data;})
          .catch(error => (console.log(error)))
    },

    select_macro(macro_row) {
      this.selectedMacroRow = macro_row
    },

    add_macro() {
      if (this.selectedActionNode === null) {
        alert("행동절차를 선택해주세요.")
        return
      }

      const selectedActionSeq = this.selectedActionNode.action.action_seq
      const newMacro = new Macro(selectedActionSeq);
      const newMacroRow = new MacroRow(newMacro)

      this.macroRowList.push(newMacroRow);
    },

    remove_macro(index) {
      this.macroRowList.splice(index, 1);
    },

    save_macro_list() {
      if (this.selectedActionNode === null) {
        alert("행동절차를 선택해주세요.")
        return
      }

      const action = Action.ofActionNode(this.selectedActionNode)
      action.macros = MacroRow.macroRowListToMacroList(this.macroRowList);

      axios.post('http://127.0.0.1:8082/api/automation/action/macro', action)
           .then(res => {alert('저장되었습니다.'); this.get_action();})
           .catch(error => (console.log(error)))
    },

    run_macro(macro_row) {
      this.runMacroParameters.macro = macro_row.macro
      this.runMacroParameters.udid = this.udid

      axios.post('http://127.0.0.1:8082/api/automation/action/macro/run', this.runMacroParameters)
          .then(res => {console.log(res)})
          .catch(error => (console.log(error)))
    },

    /**
     * element
     */
    get_element_enums() {
      axios.get('http://127.0.0.1:8082/api/utils/enum/element')
          .then(res => {this.elementEnums = res.data;})
          .catch(error => (console.log(error)))
    },
    add_element(macro_row) {
      const newElementRow = new ElementRow(new Element(macro_row.macro.macro_seq))
      macro_row.elementRows.push(newElementRow);
    },

    remove_element(index) {
      this.selectedMacroRow.elementRows.splice(index, 1);
    },

    /**
     * variable
     */
    add_variable(macro_row) {
      macro_row.variableRows.push(new VariableRow());
    },

    remove_variable(index) {
      this.selectedMacroRow.variableRows.splice(index, 1);
    },

    /**
     * 1 level 이하 tree node 불가능
     */
    onDragEnd(store) {

      function isNotMinLevel(minLevel, store) {
        if (store.placeholderLevel === minLevel) {
          return false
        }
      }

      return isNotMinLevel(1, store)
    }
  }
})
</script>

<style scoped>
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.max-h-row {
  max-height: 38px;
}
.min-w-btn {
  min-width: 40px;
}
</style>