import Element from "@/model/Element";

export default class Macro {
    macro_seq: number | null
    action_seq: number
    name: string
    driver_type: string
    element: string
    elements: Element[]
    element_type: string
    function_name: string
    variable: string
    macro_type: string
    macro_order: number
    macro_index: number
    macro_weight: number
    macro_operator: string
    min_wait_sec: number
    max_wait_sec: number
    repeat_times: number
    retry_times: number
    retry_wait_sec: number

    constructor(action_seq: number, macro: Partial<Macro> = {}) {
        this.macro_seq = macro.macro_seq ?? null;
        this.action_seq = action_seq;
        this.name = macro.name ?? '';
        this.driver_type = macro.driver_type ?? 'WEB';
        this.element = macro.element ?? '';
        this.elements = macro.elements ?? [];
        this.element_type = macro.element_type ?? 'XPATH';
        this.function_name = macro.function_name ?? '';
        this.variable = macro.variable ?? '';
        this.macro_type = macro.macro_type ?? 'CLICK';
        this.macro_order = macro.macro_order ?? 0;
        this.macro_index = macro.macro_index ?? 0;
        this.macro_weight = macro.macro_weight ?? 0;
        this.macro_operator = macro.macro_operator ?? 'NONE';
        this.min_wait_sec = macro.min_wait_sec ?? 0;
        this.max_wait_sec = macro.max_wait_sec ?? 0;
        this.repeat_times = macro.repeat_times ?? 0;
        this.retry_times = macro.retry_times ?? 10;
        this.retry_wait_sec = macro.retry_wait_sec ?? 1;
    }
}
