export default class Element {
    element_seq: number | null
    macro_seq: number
    value: string
    index: number
    type: string
    order: number

    constructor(macro_seq: number, element: Partial<Element> = {}) {
        this.element_seq = element.element_seq ?? null;
        this.macro_seq = macro_seq;
        this.value = element.value ?? '';
        this.index = element.index ?? 0;
        this.type = element.type ?? 'XPATH';
        this.order = element.order ?? 0;
    }
}
