import Action from "@/model/Action";

class ActionNode {
    $children: ActionNode[];
    children: ActionNode[];
    action: Action;
    isSelected: boolean;

    constructor(action: Action) {
        this.$children = []
        this.children = action.child_action_associations.map(child_action_association => new ActionNode(child_action_association.child_action))
        this.action = action
        this.isSelected = false
    }
}

export default ActionNode
