export class VariableRow {
    key: string;
    value: string | number;

    constructor(valueRow: Partial<VariableRow> = {}) {
        this.key = valueRow.key ?? 'key';
        this.value = valueRow.value ?? 'value';
    }

    static macroVariableToVariableRows(macroVariable: string) {
        const variableRows = new Array<VariableRow>();

        if (macroVariable !== '') {
            const variableObjects = JSON.parse(macroVariable);
            for (const key in variableObjects) {
                variableRows.push(new VariableRow({key: key, value: variableObjects[key]}))
            }
        }

        return variableRows
    }
}

export default VariableRow
