import Macro from "@/model/Macro";
import ElementRow from "@/components/automation/action/model/ElementRow";
import VariableRow from "@/components/automation/action/model/VariableRow";

export class MacroRow {
    macro: Macro;
    elementRows: ElementRow[];
    variableRows: VariableRow[];

    constructor(macro: Macro) {
        this.macro = macro;
        this.elementRows = macro.elements.map(element => new ElementRow(element));
        this.variableRows = VariableRow.macroVariableToVariableRows(this.macro.variable);
    }

    static macroRowListToMacroList(macroRowList: MacroRow[]): Macro[] {
        const macroList = macroRowList.map(value => value.toMacro());

        let order = 0;
        macroList.forEach(macro => macro.macro_order = order++)

        return macroList
    }

    toMacro() {
        this.applyMacroElements()
        this.applyMacroElementsOrder()
        this.applyMacroVariable()
        return this.macro
    }

    applyMacroElements(): void {
        this.macro.elements = this.elementRows.map(elementRow => elementRow.element)
    }

    applyMacroElementsOrder(): void {
        let order = 0
        this.macro.elements.forEach(element => element.order = order++)
    }

    applyMacroVariable(): void {
        if (this.variableRows.length == 0) {
            this.macro.variable = ''
            return
        }

        const valueObject: { [key: string]: string | number; } = {};
        this.variableRows.forEach(variableRow => valueObject[variableRow.key] = variableRow.value)
        this.macro.variable = JSON.stringify(valueObject);
    }
}

export default MacroRow
